.app_container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 30px;
}

/* navBar.js */

.logo {
  width: 50px;
  position: relative;
}
nav {
  margin-bottom: 85px;
}
nav ul {
  float: right;
}
nav ul li {
  display: inline-block;
  float: left;
}

nav ul li:not(:first-child) {
  margin-left: 48px;
}
nav ul li:last-child {
  margin-right: 85px;
}

nav ul li a {
  display: inline-block;
  outline: none;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  letter-spacing: 1.2px;
  font-weight: 600;
}

.logo-img {
  width: 80px;
  position: relative;
  top: 37px;
}

/* Intro.js */

.intro {
  font-weight: 600;
  font-size: 40px;
  margin-top: 10px;
  line-height: 1.15;
  font-family: "Merriweather", Georgia, Serif;
}

.steps-container {
  margin-top: 30px;
  margin-bottom: 65px;
}

.steps {
  position: relative;
  color: #788bff;
  font-style: normal;
  font-size: 31px;
  font-family: "Merriweather", Georgia, Serif;
  font-weight: 600;
  line-height: 1.15;
}

/* About.js */

.about-me {
  font-size: 32px;
  font-family: "Merriweather", Georgia, Serif;
}

.about-container {
  margin: 0px;
  padding: 0px;
}

.inner-container {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin-bottom: 50px;
}

.p-container {
  flex: 4;
  padding: 30px 100px 0 0;
  margin: 0;
}

.paragraph {
  line-height: 1.756;
  font-size: 18px;
  font-family: "SF Pro Display", "-apple-system", "BlinkMacSystemFont",
    "San Francisco", "Helvetica Neue", "Helvetica", "Ubuntu", "Roboto", "Noto",
    "Segoe UI", "Arial", sans-serif;
  max-width: 700px;
}

.profile-img {
  width: 100%;
  border-radius: 50%;
}

.img-container {
  flex: 2;
}

.resume {
  color: #788bff;
  position: relative;
  font-size: 20px;
}

@media screen and (max-width: 415px) {
  .profile-img {
    width: 200%;
    border-radius: 50%;
    position: relative;
    right: 80%;
  }
}

/* Projects card */

.projects {
  margin-top: 200px;
  margin-bottom: 50px;
  /* font-size: 40px; */
  font-size: 32px;
  font-family: "Merriweather", Georgia, Serif;
}

.project-body {
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1.5rem;
  margin-top: 100px;
}

.project-post {
  width: 90%;
  max-width: 98rem;
  padding: 3rem;
  background: #f9f8f8;
  box-shadow: 0 1.4rem 8rem rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  border-radius: 0.8rem;
}

.project-img-container {
  min-width: 25rem;
  max-width: 25rem;
  height: 15rem;
  transform: translateX(-8rem);
  position: relative;
}

.project-img-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 0.8rem;
}

.project-img-container::before {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 0.8rem;
}

.project-info-container {
  display: flex;
  flex-direction: column;
}

.project-info span {
  display: block;
  color: rgba(0, 0, 0, 0.5);
  font-size: 18px;
  font-weight: 600;
  margin: 0.5rem 0;
}

.project-title {
  font-size: 25px;
  margin: 1.5rem 0 2rem;
  text-transform: uppercase;
  color: #788bff;
}

.project-text {
  margin-bottom: 3rem;
  font-size: 18px;
  color: rgba(0, 0, 0, 0.7);
  margin-top: 5px;
}

.cta {
  color: #788bff;
  font-size: 20px;
}

@media screen and (max-width: 1068px) {
  .project-post {
    max-width: 80rem;
  }

  .project-img-container {
    min-width: 30rem;
    max-width: 30rem;
  }
}

@media screen and (max-width: 868px) {
  .project-post {
    flex-direction: column;
  }
}

@media screen and (max-width: 990px) {
  .project-post {
    flex-direction: column;
  }
  .project-body {
    margin-top: 100px;
    width: 80%;
  }

  .project-img-container {
    min-width: 80%;
    max-width: 80%;
    transform: translate(0, -8rem);
    margin-top: 50px;
  }
}

@media screen and (max-width: 768px) {
  .project-post {
    padding: 2.5rem;
    flex-direction: column;
  }
  .project-body {
    margin-top: 100px;
  }

  .project-img-container {
    min-width: 100%;
    max-width: 100%;
    transform: translate(0, -8rem);
    margin-top: 50px;
  }
}

@media screen and (max-width: 420px) {
  .project-post {
    padding: 1.5rem;
    flex-direction: column;
  }

  .project-img-container {
    min-width: 92%;
    max-width: 92%;
  }
}

/* Contact.js */

.form {
  position: relative;
  height: 946px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto 100px 0;
}

.form p {
  line-height: 1.756;
  font-size: 18px;
  font-family: "SF Pro Display", "-apple-system", "BlinkMacSystemFont",
    "San Francisco", "Helvetica Neue", "Helvetica", "Ubuntu", "Roboto", "Noto",
    "Segoe UI", "Arial", sans-serif;
  max-width: 700px;
  margin: 0 auto 30px;
}

.form > h1 {
  font-size: 32px;
  font-family: "Merriweather", Georgia, Serif;
  margin-bottom: 30px;
  text-align: center;
}

.form input {
  width: 60%;
  height: 45px;
}

.form > input,
textarea {
  padding: 15px;
  border-radius: 3px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137);
  margin-bottom: 20px;
  border: none;
  font-size: 16px;
  margin: 0 auto 20px;
}

.form > textarea {
  height: 120px;
  max-width: 100%;
  min-width: 60%;
  min-height: 100px;
}

.form > label {
  padding-bottom: 10px;
  color: rgb(0, 0, 32);
  font-weight: bold;
  border: none;
}

.form > button {
  padding: 10px;
  border: none;
  background-color: #788bff;
  font-weight: 500;
  font-size: 20px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  /* transition: 0.2s ease-in-out; */
  margin: 10px auto;
  width: 100px;
}

.success-msg {
  color: #57a773;
  padding: 10px 15px;
}

.err-msg {
  color: #ee6352;
  padding: 10px 15px;
}

/* Footer.js */
footer {
  display: block;
}
.line {
  position: relative;
  bottom: 200px;
}
.footer-container {
  display: flex;
  justify-content: flex-end;
  position: sticky;
  font-size: 20px;
  color: grey;
}
.footer-text {
  position: relative;
  bottom: 190px;
  color: #59656f;
  font-size: 18px;
}
.contact-logo {
  font-size: 20px;
  color: grey;
  position: relative;
  bottom: 210px;
}

/* Typewriter css */
.Typewriter__cursor {
  border-right: 2px #788bff solid;
}

/* langSwitcher.js */
select {
  padding: 2px 10px;
  color: #000;
  background-color: white;
  border: 1px solid #dddd;
  cursor: pointer;
  border-radius: 5px;
}

select:focus,
select:hover {
  outline: none;
  border: 1px solid #bbbb;
}

select option {
  background: #ffff;
}
